<template>
  <div class="mine">
    <div class="bg">
        <van-uploader :after-read="afterRead" />
    </div>
    <div class="content">
      <div class="user-info-box">
        <div class="user-info-img">
          <img :src="userInfo.userImg" />
        </div>
        <div class="user-info">
          <div class="user-info-name">{{userInfo.nickName}}</div>
        </div>
      </div>
      <div class="vip">
        <div class="user-info-desc" v-if="userInfo.desc == ''">这个人很懒，什么也没留下！</div>
        <div class="user-info-desc" v-else>{{userInfo.desc}}</div>
      </div>
      <van-cell-group>
        <van-cell
          :title="item.title"
          is-link
          v-for="(item, index) in list"
          :key="index"
          @click="goPage(item)"
        />
      </van-cell-group>
    </div>
    <van-count-down :time="time">
      <template #default="timeData">
          <span class="block">{{ timeData.hours }}</span>
          <span class="colon">:</span>
          <span class="block">{{ timeData.minutes }}</span>
          <span class="colon">:</span>
          <span class="block">{{ timeData.seconds }}</span>
      </template>
    </van-count-down>
  </div>
</template>

<script>
import "../assets/less/mine.less";
export default {
  name: "Mine",
  data() {
    return {
      userInfo: [],
      list: [
        { title: "个人资料", name: "Imformation" },
        { title: "我的订单", name: "Pay" },
        { title: "我的收藏", name: "Like" },
        { title: "收货地址", name: "ManageAddress" },
        { title: "安全中心", name: "Secure" },
      ],
      time: 30 * 60 * 60 * 1000,
    };
  },
  created(){
      this.getUserInfo();
  },
  methods: {
    black() {
      this.$router.go(-1);
    },
    afterRead(file) {
      
    },
    goPage(item) {
      this.$router.push({ name: item.name });
    },
    getUserInfo(){
        let tokenString = localStorage.getItem("token");

        if (!tokenString) {
            this.$toast("请先登录");
            return this.$router.push({ name: "Login" });
        }

        this.$toast.loading({
            message: "加载中...",
            forbidClick: true,
            duration: 0,
        });
        this.axios({
            method: 'GET',
            url: 'findMy',
            params:{
                appkey: this.appkey,
                tokenString
            }
        }).then(res => {
            this.$toast.clear();
            if(res.data.code == 'A001'){
                this.userInfo = res.data.result[0];
                
            }
        })
    }
  },
};
</script>